<template>
  <div class="searchMian">
    <form action="/">
      <van-search
        v-model="value"
        show-action
        placeholder="输入影城名称"
        @search="onSearch"
        @cancel="onCancel"
      />
      <ul v-if="value">
        <!-- 这里去遍历store里的cinemasData数据 -->
        <li v-for="item in search" :key="item.cinemaId" @click="chooseCinema(item.cinemaId)">
          <div class="cinema-info-contianer">
            <span class="cinema-info">{{ item.name }}</span>
            <span class="address cinema-info">{{ item.address }}</span>
          </div>
          <div>
            <span style="color: green">￥{{ item.lowPrice / 100 }}起</span>
            <span style="text-align: right" class="address">距离未知</span>
          </div>
        </li>
      </ul>
    </form>
  </div>
</template>
<script>
import { Search } from "vant";
export default {
  data() {
    return {
      value: "",
    };
  },
  components: {
    [Search.name]: Search,
  },
  methods: {
    onSearch() {},
    onCancel() {
      this.$router.back();
    },
  chooseCinema(cinemaId) {
      this.$router.push({
            name:'影院',
            params:{
              cinemaId
            }
          })
    },
  },
  computed: {
    search() {
      return this.$store.state.cinemasData.filter((v) => {
        return (
          v.name.toUpperCase().includes(this.value.toUpperCase()) ||
          v.address.toUpperCase().includes(this.value.toUpperCase())
        );
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.searchMian {
  min-height: 100vh;
  background-color: #fff;
}
ul {
  :last-child {
    margin-bottom: 3.0625rem;
  }
}
li {
  height: 4.7188rem;
  width: 24.375rem;
  box-sizing: border-box;
  padding: 0.9375rem;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #ededed;
  span {
    display: block;
  }
  .cinema-info-contianer {
    width: 80%;
    padding-right: 15px;
    box-sizing: border-box;
  }
  .cinema-info {
    // max-width: 80%;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
.address {
  color: #797d82;
  font-size: 0.75rem;
  margin-top: 0.3125rem;
}

.box {
  position: relative;
  //决定betterScroll滚动条的位置
  overflow: hidden;
}
</style>